import XLSX from 'xlsx'
export function readExcel (e) {//
  const files = e.target.files;
  const fileReader = new FileReader();
  return new Promise((resolve, reject) => {

    fileReader.onload = (ev) => {
      try {
        const data = ev.target.result;
        const workbook = XLSX.read(data, {
          type: 'binary'
        });
        const wsname = workbook.SheetNames[0];//
        const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]);//json
        resolve(ws)
      } catch (e) {
        reject(e)
      }
    };
    fileReader.readAsBinaryString(files[0]);
  })

}
export function readTxt (event) {
  var input = event.target;
  var reader = new FileReader();
  return new Promise((resolve, reject) => {
    reader.onload = function () {
      if (reader.result) {
        //

        let reg = /\D?(1[3456789]\d{9})\D?/g
        let phoneReg = /(1[3456789]\d{9})/
        let phoneArr = reader.result.match(reg) ? reader.result.match(reg) : []
        phoneArr = phoneArr.forEach(item => {
          return item.match(phoneReg) ? item.match(phoneReg)[0] : undefined
        })
        resolve(unique(phoneArr))
      } else {
        reject()
      }
    };
    reader.readAsText(input.files[0]);
  })
}
export function unique (arr) {
  if (arr) {
    return [...new Set(arr.map(e => JSON.stringify(e)))].map(e => JSON.parse(e))
  } else {
    return []
  }
}