<template>
  <div class="content-box">
    <Steps :current="current">
      <Step title="1"></Step>
      <Step title="2"></Step>
      <Step title="3"></Step>
    </Steps>
    <div class="task-container">
      <div class="step-content" v-if="current==0">
        <Form :model="formFirst" ref="firstStep" label-position="left" :rules="ruleValidate" :label-width="110">
          <FormItem label="" prop="msgContext">
            <div class="textBox">
              <Input v-model="formFirst.msgContext" maxlength="70" show-word-limit type="textarea" :rows="6" />
            </div>
          </FormItem>
          <FormItem>
            <p>{{formFirst.msgContext.length}}{{70-formFirst.msgContext.length}}70</p>
            <Button type="primary" class="mr-10"></Button>
          </FormItem>
          <FormItem label="">
            <DatePicker v-model="formFirst.sendTime" :editable="false" :options="options" type="daterange"
              format="yyyy-MM-dd" placeholder="," style="width: 100%"></DatePicker>
          </FormItem>
        </Form>
        <div class="flex-jc">
          <Button type="primary" @click="next" style="width:120px"></Button>
        </div>

      </div>
      <div class="step-content" v-if="current==1">
        <Form :model="formSecond" ref="secondStep" label-position="left" :rules="formSecondRule" :label-width="110">
          <FormItem label="" prop="phoneNum">
            <div class="flex">
              <cgi-file class="mr-10" txt @getFile="getTxt" ref="txtFile">
                <Button type="primary">TXT</Button>
              </cgi-file>
              <cgi-file class="mr-10" excel @getFile="getExcel" ref="excelFile">
                <Button type="primary">Excel</Button>
              </cgi-file>
            </div>
            <p>
              <Poptip title="txt" placement="bottom" class="ml-20 mr-30">
                <a>txt</a>
                <div slot="content">
                  <img src="@/assets/images/txt.jpg" alt="">
                </div>
              </Poptip>
              <Poptip title="excel" placement="bottom">
                <a>Excel</a>
                <div slot="content">
                  <img src="@/assets/images/excel.jpg" alt="">
                </div>
              </Poptip>
            </p>
            <p v-if="formSecond.phoneNum>0">
              {{selectName}}
              <span class="ml-10 mr-10">{{formSecond.phoneNum}} </span>
              <em>{{countMoney}}</em>
            </p>
          </FormItem>
        </Form>
        <div class="flex-jc">
          <Button type="primary" @click="current=0" style="width:120px;margin-right:10px"></Button>
          <Button type="primary" @click="next" style="width:120px"></Button>
        </div>
      </div>
      <div class="step-content" v-if="current==2">
        <div style="width:100%;height:400px;font-size:30px" class="flex-ac flex-jc">
          
        </div>
        <div class="flex-jc">
          <Button type="primary" @click="done" style="width:120px"></Button>
        </div>
      </div>
    </div>

  </div>
</template>
<script>
import { readTxt, readExcel, unique } from '@/utils/readFile.js'
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters(['userInfo']),
    // msgCount () {
    //   if (this.formFirst.msgContext && this.formFirst.msgContext.length <= 70) {
    //     return 1
    //   } else if (this.formFirst.msgContext.length > 70) {
    //     return Math.ceil(this.formFirst.msgContext.length / 67)
    //   } else {
    //     return 1
    //   }
    // },
    countMoney () {
      return ((this.formSecond.phoneNum * 0.4).toFixed(2))
    },
  },
  data () {
    const validatephoneNum = (rule, value, callback) => {
      if (value == 0) {
        callback(new Error(''));
      } else {
        callback()
      }
    }
    return {
      current: 0,
      formFirst: {
        msgContext: '',
        sendTime: '',
      },
      ruleValidate: {
        msgContext: [
          { required: true, message: '', trigger: 'blur' }
        ],
      },
      formSecond: {
        phoneNum: 0,
        arr_num: []
      },
      formSecondRule: {
        phoneNum: [
          { required: true, validator: validatephoneNum }
        ]
      },
      selectName: '',
      options: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000;
        },
      }
    }
  },
  methods: {
    next () {
      if (this.current == 0) {
        this.$refs.firstStep.validate(async (valid) => {
          if (valid) {
            this.current = 1
          }
        })
      } else if (this.current == 1) {
        this.$refs.secondStep.validate(async (valid) => {
          if (valid) {
            let params = {
              context: this.formFirst.msgContext,
              contact_list: this.formSecond.arr_num,
              mch_phone_num: this.userInfo.phone_num,
              beg_time: this.formFirst.sendTime[0] ? this.formFirst.sendTime[0].getTime() / 1000 : undefined,
              end_time: this.formFirst.sendTime[1] ? this.formFirst.sendTime[1].getTime() / 1000 : undefined,
            }
            let resData = await this.$post('cgi/pub_mission', params)
            if (resData.ret_code == 0) {
              this.current = 2
            }
          }
        })
      }
    },
    async getTxt (e) {
      this.selectName = e.target.files[0].name;
      let arrPhone = await readTxt(e)
      this.formSecond.phoneNum = arrPhone.length
      if (this.formSecond.phoneNum == 0) {
        return this.$Message.error('')
      }
      this.formSecond.arr_num = arrPhone.map((item) => {
        return { phone_num: item + '' }
      })
      this.$refs.secondStep.validateField('phoneNum')
    },
    async getExcel (e) {
      this.selectName = e.target.files[0].name;
      let self = this;
      this.excelData = await readExcel(e)
      let phoneKey = ''
      Object.keys(self.excelData[0]).forEach(function (key) {
        if (/^1[3456789]\d{9}$/.test(self.excelData[0][key])) {
          phoneKey = key
        }
      })
      if (phoneKey) {
        this.formSecond.arr_num = this.excelData.map((item) => {
          return { phone_num: item[phoneKey] + '' }
        })
        this.formSecond.arr_num = unique(this.formSecond.arr_num)
        this.formSecond.phoneNum = this.formSecond.arr_num.length
      } else {
        this.$Message.error('')
        this.formSecond.phoneNum = 0
      }
      this.$refs.secondStep.validateField('phoneNum')
    },
    done () {
      this.$router.push('/msg')
    },

  }
}
</script>
<style lang="less" scoped>
.task-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  .step-content {
    width: 600px;
    padding: 30px;
    display: flex;
    flex-direction: column;
  }
}
</style>